import VueSnackbar from "vue-snack";
Vue.use(VueSnackbar, { close: true, position: 'bottom-left' });

// in your component file
export default {
	methods: {
		success () {
			this.$snack.success({
				text: 'We are using Vue Snackbar',
				button: null,
				action: null
			});
		}
	}
}`;

const cssCode = `// assets/css/_vuesnackbar.scss

.snackbar {
  --primary: #3490dc;
  --background: #22292f;
  --text: #e3e3e3;
  --font: "Inter UI", sans-serif;
  --times: 1rem;
  --padd: 1rem;
  font-size: 1rem;
  font-weight: normal;
  background-color: var(--background);
  color: var(--text);
  font-family: var(--font);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: fixed;
  min-height: var(--times);
  padding: var(--padd);
  border-radius: 8px;
  z-index: 2500;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.12), 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
@media screen and (max-width: 576px) {
  .snackbar {
    border-radius: 0;
    width: calc(100% - (2 * var(--padd)));
  }
}
@media screen and (min-width: 576px) {
  .snackbar.bottom,
  .snackbar.top {
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    left: 50%;
  }
  .snackbar.bottom-left,
  .snackbar.top-left {
    left: 1rem;
  }
  .snackbar.bottom-right,
  .snackbar.top-right {
    right: 1rem;
  }
}
.snackbar[class*="bottom"] {
  bottom: 1rem;
}
.snackbar[class*="top"] {
  top: 1rem;
}
@media screen and (max-width: 576px) {
  .snackbar {
    left: 0;
  }
  .snackbar[class*="bottom"] {
    bottom: 0;
  }
  .snackbar[class*="top"] {
    top: 0;
  }
}
.snackbar .snackbar__text {
  -webkit-box-flex: 1;
  -ms-flex: 1;
  flex: 1;
}
.snackbar .snackbar__action {
  text-transform: uppercase;
  font-weight: 500;
  margin: 0 0 0 1rem;
  color: var(--primary);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.snackbar .times {
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  height: var(--times);
  width: var(--times);
  margin-left: 1rem;
  -webkit-transform: scale(1.25);
  transform: scale(1.25);
  position: relative;
}
.snackbar .times:before {
  display: block;
  content: "";
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: scale(0.9);
  transform: scale(0.9);
  border-radius: 50%;
  -webkit-transition: -webkit-transform 0.1s ease-in;
  transition: transform 0.1s ease-in;
  transition: transform 0.1s ease-in, -webkit-transform 0.1s ease-in;
}
.snackbar .times:hover:before {
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-transform: scale(2);
  transform: scale(2);
}
.snack-enter-active,
.snack-leave-active {
  -webkit-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.snack-enter,
.snack-leave-to {
  opacity: 0;
}
@media screen and (min-width: 576px) {
  .snack-enter.bottom,
  .snack-leave-to.bottom {
    -webkit-transform: translateY(100%) translateX(-50%);
    transform: translateY(100%) translateX(-50%);
  }
  .snack-enter.top,
  .snack-leave-to.top {
    -webkit-transform: translateY(-100%) translateX(-50%);
    transform: translateY(-100%) translateX(-50%);
  }
}
@media screen and (max-width: 576px) {
  .snack-enter[class*="top"],
  .snack-leave-to[class*="top"] {
